body {
    background-color: #F8F6F0;
    padding: 0px;
    margin: 0px;

    
}
.backgroundImage {
    position: relative;
    width: 100vw;
    height: 100%;
    z-index: 1;
}
.backgroundImage::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    /* Adjust the opacity value */
    z-index: -1; /* Ensure it is behind the content */
}
.above {
    z-index: 2;
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 15rem;
    background-color: black;
    padding: 0 10rem;
    z-index: 3;
    opacity: 0.40;
}
.logo {
    height: 12rem;
    width: 15rem;


}
nav {
    display: flex;
    gap: 15px;
    position: absolute;
    top: 200px;
    left: 250px;
    margin-left: 20px;
    margin-top: 5px;
    
}
button {
    background-color: #47474785; /* Ensure buttons have a solid background color */
    border: none;
    padding: 10px 20px;
