    width: 12rem;
    font-size: 1.8rem;
    font-family: 'Homemade Apple';
    color: rgb(250, 250, 250);
    display: flex; 
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease;
    
}
button:hover {
    background-color: rgba(148, 143, 143, 0.733);
}

#content {
    width: 80vw;
    height: 100%;
    background-color: #4747476e;
   
    
}
.menuBody {
    display: flex;
    justify-content: center;
    align-items: center;
