___CSS_LOADER_EXPORT___.push([module.id, `.menu-item {
    display: flex;
    flex-direction: column;
    border: 1px solid #ccc;
    margin: 60px 10px 10px 10px;
    padding: 10px;
    background-color: #f9f9f9;
    width: 28rem;
    position: relative;
    margin-top: 150px;
}
.menu-item .image {
    height: 270px;
    width: 270px;
    position: absolute;
    top: -125px; /* Move the image up by half its height */
    left: 50%; /* Center the image horizontally */
    transform: translateX(-50%); /* Adjust the image to stick out from the center */
    
    z-index: 1; /* Ensure the image is above other elements */
    
}
.menu-item .background {
    display: flex;
    flex-direction: column;
    padding: 10px;
}
.menu-item .background .border-shadow {
    border-bottom: 1px solid grey;
    box-shadow: 0 4px 2px -2px grey;
}
.menu-item .background .rating-calories {
    display: flex;
    justify-content: space-between;
}
.menuContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    

}
.menuTitle {
    font-size: 2rem;
    font-family: Homemade Apple;
    margin: 0;
}
.menuDescription {
    font-size: 1.3rem;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 0;
    margin-bottom: 40px;
}
.titleAndDescriptionDiv {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.image {
    
    background-size: cover;
    background-position: center;

}
.background {
    margin-top: 150px;
}
.caloriesText, .ratingText {
    font-size: 1.2rem;
    margin-top: 2rem;
}`, "",{"version":3,"sources":["webpack://./src/menu.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,sBAAsB;IACtB,2BAA2B;IAC3B,aAAa;IACb,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,WAAW,EAAE,yCAAyC;IACtD,SAAS,EAAE,kCAAkC;IAC7C,2BAA2B,EAAE,kDAAkD;;IAE/E,UAAU,EAAE,6CAA6C;;AAE7D;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;AACjB;AACA;IACI,6BAA6B;IAC7B,+BAA+B;AACnC;AACA;IACI,aAAa;IACb,8BAA8B;AAClC;AACA;IACI,aAAa;IACb,eAAe;IACf,uBAAuB;;;AAG3B;AACA;IACI,eAAe;IACf,2BAA2B;IAC3B,SAAS;AACb;AACA;IACI,iBAAiB;IACjB,yCAAyC;IACzC,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;AACA;;IAEI,sBAAsB;IACtB,2BAA2B;;AAE/B;AACA;IACI,iBAAiB;AACrB;AACA;IACI,iBAAiB;IACjB,gBAAgB;AACpB","sourcesContent":[".menu-item {\n    display: flex;\n    flex-direction: column;\n    border: 1px solid #ccc;\n    margin: 60px 10px 10px 10px;\n    padding: 10px;\n    background-color: #f9f9f9;\n    width: 28rem;\n    position: relative;\n    margin-top: 150px;\n}\n.menu-item .image {\n    height: 270px;\n    width: 270px;\n    position: absolute;\n    top: -125px; /* Move the image up by half its height */\n    left: 50%; /* Center the image horizontally */\n    transform: translateX(-50%); /* Adjust the image to stick out from the center */\n    \n    z-index: 1; /* Ensure the image is above other elements */\n    \n}\n.menu-item .background {\n    display: flex;\n    flex-direction: column;\n    padding: 10px;\n}\n.menu-item .background .border-shadow {\n    border-bottom: 1px solid grey;\n    box-shadow: 0 4px 2px -2px grey;\n}\n.menu-item .background .rating-calories {\n    display: flex;\n    justify-content: space-between;\n}\n.menuContainer {\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    \n\n}\n.menuTitle {\n    font-size: 2rem;\n    font-family: Homemade Apple;\n    margin: 0;\n}\n.menuDescription {\n    font-size: 1.3rem;\n    font-family: Arial, Helvetica, sans-serif;\n    margin-t